import requestUtil from '@/utils/request'

// 点击购物车结算或立即购买后跳转到提交订单页时调用-订单结算
export const checkoutOrderApi = (obj) => {
  return requestUtil({
    url: '/checkout/order',
    params: {
      ...obj
    }
  })
}

// 提交订单
export const submitOrderApi = (obj) => {
  return requestUtil({
    method: 'post',
    url: '/checkout/submit',
    data: {
      ...obj
    }
  })
}
