<template>
  <div class="pay" v-if="order.orderTotalNum">
    <van-nav-bar
      fixed
      title="订单结算台"
      left-arrow
      @click-left="$router.back()"
    />

    <!-- 地址相关 -->
    <div class="address" @click="selNewAdd">
      <div class="left-icon">
        <van-icon name="logistics" />
      </div>

      <div class="info" v-if="addressShownData.region">
        <div class="info-content">
          <span class="name">{{ addressShownData.name + " " }}</span>
          <span class="mobile">{{ addressShownData.phone }}</span>
        </div>
        <div class="info-address">
          {{
            addressShownData.region.province +
            " " +
            addressShownData.region.city +
            " " +
            addressShownData.region.region +
            " " +
            addressShownData.detail
          }}
        </div>
      </div>

      <div class="info" v-else>请选择配送地址</div>

      <div class="right-icon">
        <van-icon name="arrow" />
      </div>
    </div>

    <!-- 订单明细 -->
    <div class="pay-list">
      <div class="list">
        <div
          class="goods-item"
          v-for="(item, index) in order.goodsList"
          :key="index"
        >
          <div class="left">
            <img :src="item.goods_image" alt="" />
          </div>
          <div class="right">
            <p class="tit text-ellipsis-2">
              {{ item.goods_name }}
            </p>
            <p class="info">
              <span class="count">x{{ item.total_num }}</span>
              <span class="price">¥{{ item.goods_price_min }}</span>
            </p>
          </div>
        </div>
      </div>

      <div class="flow-num-box">
        <span>共 {{ order.orderTotalNum }} 件商品，合计：</span>
        <span class="money">￥{{ order.orderTotalPrice }}</span>
      </div>

      <div class="pay-detail">
        <div class="pay-cell">
          <span>订单总金额：</span>
          <span class="red">￥{{ order.orderTotalPrice }}</span>
        </div>

        <!-- 优惠券 -->
        <div class="pay-cell">
          <span class="">优惠券：</span>
          <div class="">
            <div
              v-if="order.couponList.length"
              @click="isShowAction = true"
              class="choose-coupon red"
            >
              <span class="" v-if="order.couponId > 0"
                >-￥{{ order.couponMoney }}</span
              >
              <span class="" v-else
                >有{{ order.couponList.length }}张优惠券</span
              >
              <van-icon name="arrow" />
            </div>
            <span v-else class="">无优惠券可用</span>
          </div>
        </div>

        <!-- 展示优惠券 -->
        <van-action-sheet v-model="isShowAction" title="选择优惠券">
          <MyCouponList
            v-if="order.couponList.length"
            :myCouponList="order.couponList"
            @onClickCoupon="onUsingCoupon"
            class="coupon-list"
          ></MyCouponList>
          <div class="no-coupon" @click="noUsingCoupon">
            <span>不使用优惠券</span>
          </div>
        </van-action-sheet>

        <!-- 配送费用 快递 -->
        <div class="pay-cell">
          <span>配送费用：</span>
          <span v-if="addressShownData.region">请先选择配送地址</span>
          <span v-else class="red">+￥{{ order.expressPrice }}</span>
        </div>
      </div>

      <!-- 支付方式 -->
      <div class="pay-way">
        <span class="tit">支付方式</span>
        <van-radio-group v-model="payType" checked-color="#ee0a24">
          <van-cell-group>
            <van-cell clickable @click="payType = '10'">
              <template #title>
                <van-icon name="balance-o" color="#ff9700"></van-icon>
                <span>余额支付（可用 ¥ {{ personal.balance }}）</span>
              </template>
              <template #right-icon>
                <van-radio name="10" />
              </template>
            </van-cell>

            <van-cell clickable @click="payType = '0'">
              <template #title>
                <van-icon name="info-o" color="#ee0a24"></van-icon>
                <span>稍后支付</span>
              </template>
              <template #right-icon>
                <van-radio name="0" />
              </template>
            </van-cell>
          </van-cell-group>
        </van-radio-group>
        <!-- <div class="pay-cell">
        </div> -->
      </div>

      <!-- 买家留言 -->
      <div class="buytips">
        <textarea
          maxlength="50"
          placeholder="选填：买家留言（50字内）"
          name=""
          id=""
          cols="30"
          rows="10"
          v-model="remark"
        ></textarea>
      </div>
    </div>

    <!-- 底部提交 -->
    <div class="footer-fixed">
      <div class="left">
        实付款：<span>￥{{ order.orderPayPrice }}</span>
      </div>
      <div class="tipsbtn" @click="submitOrdr">提交订单</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { checkoutOrderApi, submitOrderApi } from '@/api/orderCheckout'
import { getAddDetailApi } from '@/api/address'
import MyCouponList from '@/components/MyCouponList.vue'

export default {
  name: 'CheckoutIndex',
  components: {
    MyCouponList
  },

  data () {
    return {
      isShowAction: false,

      mode: this.$route.query.mode,
      addressId: 0,
      defaultAddId: 0,
      // 当前页面显示的地址ID
      addressShownData: {},

      // 通用的支付相关参数
      orderPayData: {
        delivery: 10,
        couponId: 0,
        isUsePoints: 0
      },

      // 提交订单需要的额外参数
      payType: '0',
      remark: '',

      order: {},
      personal: {}

    }
  },

  computed: {
    // 购物车结算与立即购买提交不同的参数
    goodsInfo () {
      if (this.mode === 'cart') {
        return {
          cartIds: this.$route.query.cartIds
        }
      } else {
        return {
          goodsId: this.$route.query.goodsId,
          goodsNum: this.$route.query.goodsNum,
          goodsSkuId: this.$route.query.goodsSkuId
        }
      }
    },

    ...mapState('address', ['chosenAddId'])
  },

  methods: {
    // 使用参数获取订单信息
    async checkoutOrder () {
      // 参数
      const obj = {
        mode: this.mode,
        ...this.orderPayData,
        ...this.goodsInfo
      }
      const { data } = await checkoutOrderApi(obj)
      // console.log(data)
      this.order = data.order
      this.personal = data.personal
      // 有默认地址信息则赋值，否则为0
      this.defaultAddId = data.order.address
        ? data.order.address.address_id
        : 0

      // 在这个页面中不需要将Vuex中的defaultAddId赋值，因为没有对应的业务
      // chosenAddId === 0，则为新加载页面，使用默认地址
      const addressId = this.chosenAddId ? this.chosenAddId : this.defaultAddId
      await this.getAddDetail(addressId)
    },

    // 获取地址详情
    async getAddDetail (addressId) {
      // 如果ID是0则不获取
      if (!addressId) {
        return
      }
      // 根据ID获取详情
      const {
        data: { detail }
      } = await getAddDetailApi(addressId)
      this.addressShownData = detail
    },

    // 提交订单
    async submitOrdr () {
      const obj = {
        mode: this.mode,
        ...this.orderPayData,
        ...this.goodsInfo,
        payType: +this.payType,
        remark: this.remark
      }
      const { data: { orderId } } = await submitOrderApi(obj)
      this.$router.replace(`/orderdetail?orderId=${orderId}`)
    },

    // 点击优惠券则使用该优惠券
    async onUsingCoupon (couponId) {
      this.orderPayData.couponId = couponId
      await this.checkoutOrder()
      this.isShowAction = false
    },

    // 不使用优惠券
    async noUsingCoupon () {
      this.orderPayData.couponId = 0
      await this.checkoutOrder()
      this.isShowAction = false
    },

    // 在地址列表页选择收货地址
    selNewAdd () {
      this.$router.push({
        path: '/address',
        query: { backUrl: this.$route.fullPath }
      })
    }
  },

  created () {
    // 获取订单详情
    this.checkoutOrder()
  }

}
</script>

<style lang="less" scoped>
.choose-coupon {
  padding-left: 60px;
}
.pay {
  padding-top: 48px;
  padding-bottom: 48px;
  ::v-deep {
    .van-nav-bar__arrow {
      color: #333;
    }
  }
}
.address {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  font-size: 14px;
  color: #666;
  position: relative;
  background: url(@/assets/border-line.png) bottom repeat-x;
  background-size: 60px auto;
  .left-icon {
    margin-right: 20px;
  }
  .right-icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-7px);
  }
}
.goods-item {
  height: 100px;
  margin-bottom: 6px;
  padding: 10px;
  background-color: #fff;
  display: flex;
  .left {
    width: 100px;
    img {
      display: block;
      width: 80px;
      margin: 10px auto;
    }
  }
  .right {
    flex: 1;
    font-size: 14px;
    line-height: 1.3;
    padding: 10px;
    padding-right: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: #333;
    .info {
      margin-top: 5px;
      display: flex;
      justify-content: space-between;
      .price {
        color: #fa2209;
      }
    }
  }
}

.flow-num-box {
  display: flex;
  justify-content: flex-end;
  padding: 10px 10px;
  font-size: 14px;
  border-bottom: 1px solid #efefef;
  .money {
    color: #fa2209;
  }
}

.pay-cell {
  font-size: 14px;
  padding: 10px 12px;
  color: #333;
  display: flex;
  justify-content: space-between;
  .red {
    color: #fa2209;
  }
}
.pay-detail {
  border-bottom: 1px solid #efefef;
}

.coupon-list {
  padding: 0 16px;
}
.no-coupon {
  display: flex;
  justify-content: center;
  margin: 90px auto 30px;
  width: 300px;
  height: 36px;
  border: 0.5px solid #e3e3e3;
  border-radius: 5px;
  line-height: 36px;
  color: #888;
}
.pay-way {
  font-size: 14px;
  padding: 10px 12px;
  border-bottom: 1px solid #efefef;
  color: #333;
  .tit {
    line-height: 30px;
  }
  // .pay-cell {
  //   padding: 10px 0;
  // }
  .van-icon {
    font-size: 20px;
    margin-right: 5px;
  }
}

.buytips {
  display: block;
  textarea {
    display: block;
    width: 100%;
    border: none;
    font-size: 14px;
    padding: 12px;
    height: 100px;
  }
}

.footer-fixed {
  position: fixed;
  background-color: #fff;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 46px;
  line-height: 46px;
  border-top: 1px solid #efefef;
  font-size: 14px;
  display: flex;
  .left {
    flex: 1;
    padding-left: 12px;
    color: #666;
    span {
      color: #fa2209;
    }
  }
  .tipsbtn {
    width: 121px;
    background: linear-gradient(90deg, #f9211c, #ff6335);
    color: #fff;
    text-align: center;
    line-height: 46px;
    display: block;
    font-size: 14px;
  }
}
</style>
